.newsletter {
  background: url('../img/newsBg.png') center;
  background-size: cover;
  padding: 60px 0;
  .news {
    .sm(text-align,
    center);
    h1 {
      font-size: 32px;
      font-family: "Open Sans";
      color: #ff6868;
      text-transform: uppercase;
      font-weight: 900;
      margin-left: 40px;
      margin-bottom: 20px;
      .md(margin-left,
      0);
    }
    p {
      font-weight: 300;
      font-size: 14px;
      color: #fff;
      line-height: 1.5;
      text-align: left;
      max-width: 423px;
      margin-bottom: 20px;
    }
    form {
      display: flex;
      justify-content: space-between;
      max-width: 525px;
      input {
        border: none;
        border-radius: 3px;
        background-color: rgb(74, 81, 91);
        width: 70%;
        height: 46px;
        padding-left: 25px;
        &::placeholder {
          font-size: 12px;
          font-family: "Open Sans";
          color: rgb(162, 162, 162);
          font-weight: bold;
          text-transform: uppercase;
        }
      }
      button {
        border: none;
        border-radius: 8px;
        background-color: rgb(255, 104, 104);
        width: 25%;
        height: 46px;
        font-size: 12px;
        font-family: "Open Sans";
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: uppercase;
        display: block;
      }
    }
  }
}