.design {
  background: url('../img/desinger.png') center;
  background-size: cover;
  position: relative;
  padding: 60px 0;
  .right {
    position: absolute;
    right: 0;
  }
  .left {
    position: absolute;
    left: 0;
  }
  .designer {
    display: flex;
    justify-content: center;
    position: relative;
    flex-wrap: wrap;
    img {
      margin-right: 10px;
    }
    .info {
      margin-left: 10px;
      .md(text-align,
      center);
      h4 {
        font-size: 32px;
        font-family: "Open Sans";
        color: #ff6868;
        text-transform: uppercase;
        font-weight: bold;
        margin: 0;
      }
      h6 {
        font-size: 24px;
        font-family: "Open Sans";
        color: rgb(255, 255, 255);
        line-height: 1.2;
        text-transform: capitalize;
      }
      p {
        font-size: 14px;
        color: rgb(255, 255, 255);
        line-height: 1.5;
        text-align: left;
        max-width: 458px;
      }
      a {
        text-decoration: none;
        .media-block( {
          text-align: center;
        }
        ,
        320px);
        i {
          color: #ff6868;
          border: #ff6868 2px solid;
          line-height: 2;
          border-radius: 50%;
          text-align: center;
          width: 35px;
          height: 35px;
          margin: 0 10px;
          &:nth-child(1) {
            margin-left: 0;
          }
          &:hover {
            background: #ff6868;
            color: #39414b;
          }
        }
      }
      .bar {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        margin-bottom: 15px;
        h3 {
          font-size: 14px;
          font-family: "Open Sans";
          color: rgb(255, 255, 255);
          font-weight: bold;
          text-transform: uppercase;
          margin: 0;
        }
        p {
          font-size: 14px;
          font-family: "Open Sans";
          color: rgb(255, 104, 104);
          font-weight: bold;
          margin: 0;
        }
      }
      .progress {
        height: 5px;
        background-color: #4a515b;
        .progress-bar {
          background-color: #da6365;
        }
      }
    }
  }
}