footer {
  background: #38404a;
  padding: 60px 0;
  .footerTitle {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .logotip {
      img {}
      hr {
        background-color: #49515c;
        margin: 5px 0;
      }
      p {
        span {
          color: #ff6868;
        }
        color: #99a0a8;
        font-size: 12px;
        font-family: "Myriad Pro";
      }
    }
    a {
      align-self: center;
      text-decoration: none;
      i {
        font-size: 12px;
        color: #ff6868;
        border: #ff6868 2px solid;
        line-height: 2;
        border-radius: 50%;
        text-align: center;
        width: 25px;
        height: 25px;
        &:nth-child(2) {
          margin: 0 8px;
        }
        &:hover {
          background: #ff6868;
          color: #39414b;
        }
      }
    }
  }
}