header {
  min-height: 100vh;
  background: url('../img/headerBg.png') center;
  background-size: cover;
  position: relative;
  .sina-nav {
    background: url('../img/menubg.png')  ;
    border-top: 1px solid #ff6868 !important;
    .sina-brand {
      h2 {
        font-size: 22px;
        font-family: "Open Sans";
        color: rgb(255, 104, 104);
        text-transform: uppercase;
        font-weight: bold;
        line-height: 2.5;
      }
    }
    .sina-menu {
      li {
        a {
          font-size: 14px;
          font-family: "Open Sans";
          color: rgb(255, 255, 255);
          font-weight: bold;
          text-transform: uppercase;
        }
      }
    }
    .navbar-collapse {
      .media-block({
          background: url('../img/menubg.png') ;
          background-size: cover;
      },1024px);
    
    }
  }
  .headerTitle {
      max-width: 475px;
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 30%;
      .media-block({
       top: 20%;
    },320px);
    h1 {
      font-size: 52px;
      font-family: "Open Sans";
      color: #ff6868;
      text-transform: uppercase;
      font-weight: bold;
      margin-bottom: 30px;
    }
    p {
      font-size: 14px;
      font-family: "Open Sans";
      color: rgb(255, 255, 255);
      line-height: 1.5;
      text-align: left;
      max-width: 423px;
      margin-bottom: 30px;
    }
    form {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
      input {
        border: none;
        border-radius: 3px;
        background-color: rgb(74, 81, 91);
       width: 70%;
        height: 46px;
        padding-left: 25px;
        &::placeholder {
          font-size: 12px;
          font-family: "Open Sans";
          color: rgb(162, 162, 162);
          font-weight: bold;
          text-transform: uppercase;
        }
      }
      button {
        border: none;
        border-radius: 8px;
        background-color: rgb(255, 104, 104);
        width: 25%;
        height: 46px;
        font-size: 12px;
        font-family: "Open Sans";
        color: rgb(255, 255, 255);
        font-weight: bold;
        text-transform: uppercase;
        display: block;
      }
    }
    a{
        text-decoration: none;
        .media-block({
            text-align: center;
        },320px);
        i{
            color: #ff6868;
            border: #ff6868 2px solid;
            line-height: 2.5;
           border-radius: 50%;
           text-align: center;
            width: 44px;
            height: 44px;
            margin:0 10px;
            &:nth-child(1){
                margin-left: 0;
            }
            &:hover{
                background: #ff6868;
                color: #39414b;
            }
        }
    }
  }
}