.devolopment {
 padding-bottom: 90px;
 margin-top: 90px;
 position: relative;
 .iphone{
     position: absolute;
     .lg(top, 15%); .md(top, 30%);.sm(top, 55%);.media-block({
         top: 70%;
     },400px);
 }
  .app {
    h3 {
      font-size: 32px;
      font-family: "Open Sans";
      color: #ff6868;
      text-transform: uppercase;
      font-weight: 900;
    }
    p {
      font-size: 14px;
      color: rgb(153, 160, 168);
      line-height: 1.5;
      max-width: 458px;
    }
    button {
      border-radius: 8px;
      background-color: rgb(255, 104, 104);
      max-width: 132px;
      height: 46px;
      font-size: 12px;
      font-family: "Open Sans";
      color: rgb(255, 255, 255);
      font-weight: bold;
      text-transform: uppercase;
      border: none;
    }
  }
}