.blog{
    padding: 90px 0;
    .content{
        margin-top: 55px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .item{
            max-width: 450px;
            display: flex;
            margin: 10px 15px;
            .sm-block({
              flex-direction: column;
              text-align: center;  
            });
           
            .users{
                margin-right: 10px;
                .sm-block({
                  margin: 0;
                  });
            }
            .info{
                margin-left: 10px;
                .sm-block({
                    margin: 0;
                    });
                h3{
                        font-size: 24px;
                        font-family: "Open Sans";
                        color: rgb(58, 66, 76);
                        text-transform: uppercase;
                        font-weight: 900;
                       &:hover{
                           color: #ff6868;
                       }
                }
                h6{
                        font-size: 12px;
                        font-family: "Open Sans";
                        color: rgb(221, 221, 221);
                        font-weight: bold;
                        text-transform: uppercase;
                }
                p{
                        font-size: 14px;
                        color: rgb(153, 160, 168);
                        line-height: 1.5;
                        max-width: 213px;
                }
            }
        }
    }
}