.comment {
  padding: 60px 0;
  background: #38404a;
  .post {
    text-align: center;
    display: flex;
    flex-direction: column;
    h4 {
      font-size: 32px;
      font-family: "Open Sans";
      color: rgb(255, 255, 255);
      max-width: 743px;
      margin: auto;
      margin-bottom: 20px;
    }
    a {
      margin-bottom: 20px;
      text-decoration: none;
      font-size: 17px;
      color: rgb(255, 255, 255);
      font-weight: 400;
      span {
        color: #99a0a8;
      }
    }
    i {
      color: #ff6868;
      border: #ff6868 2px solid;
      line-height: 2.5;
      border-radius: 50%;
      margin: auto;
      width: 44px;
      height: 44px;
      &:hover {
        background: #ff6868;
        color: #39414b;
      }
    }
  }
}