.pricing {
  padding: 90px 0;
  .content {
    display: flex;
    justify-content: center;
    .md(flex-wrap, wrap);
    .card {
      width: 300px;
      margin: 55px 10px;
      .card-top {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        background-image: -moz-linear-gradient( -45deg, rgb(56, 64, 74) 0%, rgb(61, 69, 79) 100%);
        background-image: -webkit-linear-gradient( -45deg, rgb(56, 64, 74) 0%, rgb(61, 69, 79) 100%);
        background-image: -ms-linear-gradient( -45deg, rgb(56, 64, 74) 0%, rgb(61, 69, 79) 100%);
        display: flex;
        justify-content: space-between;
        padding: 45px 24px;
        .tarif {
          h1 {
            font-size: 28px;
            font-family: "Open Sans";
            color: #ff6868;
            text-transform: uppercase;
            font-weight: 900;
            margin: 0;
          }
          p {
            font-size: 12px;
            font-family: "Open Sans";
            color: rgb(113, 122, 132);
            text-transform: uppercase;
            margin: 0;
          }
        }
        .price {
          font-size: 32px;
          font-family: "Open Sans";
          color: rgb(255, 104, 104);
          font-weight: bold;
          margin: 0;
        }
      }
      .card-body {
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        ul {
          padding: 0;
          li {
            list-style: none;
            text-transform: capitalize;
            margin-bottom: 10px;
            .fa {
              color: #ff6868;
              margin-right: 14px;
              font-size: 20px;
            }
            font-size: 16px;
            font-family: "Open Sans";
            color: rgb(153, 160, 168);
          }
        }
        button {
          display: block;
          margin: auto;
          border: none;
          border-radius: 8px;
          background-color: rgb(255, 104, 104);
          width: 102px;
          height: 46px;
          font-size: 12px;
          font-family: "Open Sans";
          color: rgb(255, 255, 255);
          font-weight: bold;
          text-transform: uppercase;
        }
      }
    }
  }
}