.contact {
  padding: 90px 0;
  form {
    margin-top: 40px;
    margin-bottom: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    textarea {
      width: 78%;
      height: 250px;
      .sm(height, 150px);
      margin: 10px;
      border: none;
      border-radius: 3px;
      background: url('../img/contactinput.png');
      padding-left: 25px;
      padding-top: 25px;
      &::placeholder {
        font-size: 12px;
        font-family: "Open Sans";
        color: #a2a2a2;
        font-weight: bold;
        text-transform: uppercase;
      }
    }
    input {
      margin: 10px;
      border: none;
      border-radius: 3px;
      background: url('../img/contactinput.png');
      width: 25%;
      .md(width,
      78%);
      height: 46px;
      padding-left: 25px;
      &::placeholder {
        font-size: 12px;
        font-family: "Open Sans";
        color: #a2a2a2;
        font-weight: bold;
        text-transform: uppercase;
      }
    }
  }
  button {
    display: block;
    margin: auto;
    border: none;
    border-radius: 8px;
    background-color: rgb(255, 104, 104);
    width: 102px;
    height: 46px;
    font-size: 12px;
    font-family: "Open Sans";
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-transform: uppercase;
  }
}