.service {
  padding: 90px 0;
  
  .shape {
    display: block;
    margin: 20px auto;
    .lg(width, 70%);
    .sm-block( {
      opacity: 0;
    }
    );
  }
  .content {
    display: flex;
    justify-content: center;
    .sm-block( {
      flex-wrap: wrap;
    }
    );
    .item {
      display: flex;
      flex-direction: column;
      max-width: 290px;
      margin: 0 15px;
      text-align: center;
      img {
        margin-bottom: 25px !important;
        display: block;
        margin: auto;
      }
      h2 {
        margin-top: auto;
        font-size: 24px;
        font-family: "Open Sans";
        color: rgb(58, 66, 76);
      }
      p {
        margin-top: auto;
        font-size: 14px;
        color: rgb(154, 160, 168);
        line-height: 1.5;
        text-align: center;
        max-width: 294px;
        margin-bottom: 55px;
      }
    }
  }
}