.portfolio {
  padding-top: 90px;
  .title {
    p {
      max-width: 700px;
      margin: auto;
    }
  }
  .nav {
    justify-content: center;
    padding: 70px 0;
    li {
      border-radius: 8px;
      padding: 7px 16px;
      border: 1.5px solid #fff;
      &:hover {
        a {
          color: #ff6868;
        }
        border-color: #ff6868;
      }
      &.active {
        background: #ff6868;
        a {
          color: #fff !important;
        }
      }
      a {
        text-transform: capitalize;
        font-size: 16px;
        font-family: "Open Sans";
        color: #99a0a8;
        text-align: center;
        text-decoration: none;
      }
    }
  }
  .tab-content {
    .tab-pane {
      display: none;
      &.active {
        display: flex;
        justify-content: center;
      }
      flex-wrap: wrap;
      .item {
        width: 25%;
        position: relative;
        display: block;
        img {
          width: 100%;
          object-fit: cover;
        }
        &:hover {
          .overlay {
            opacity: 1;
          }
          .link-icon {
            opacity: 1;
          }
        }
        img {
          cursor: pointer;
        }
        .overlay {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
          background: url('../img/overlay.png') repeat;
        }
        .link-icon {
          display: block;
          color: #fff;
          font-size: 40px;
          line-height: 40px;
          text-align: center;
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
          position: absolute;
          top: 45%;
          left: 20px;
          margin-left: -18.5px;
          margin-top: -20px;
          width: 100%;
          p {
            font-family: 'Open Sans';
            font-size: 24px;
            font-weight: 700;
            color: #ff6868;
            text-transform: uppercase;
            .md(display,
            none);
          }
          .fa {
            font-size: 24px;
            font-weight: 700;
            color: #ff6868;
            text-transform: uppercase;
          }
        }
      }
    }
  }
}